
Template.__checkName("languageSwitcher");
Template["languageSwitcher"] = new Template("Template.languageSwitcher", (function() {
  var view = this;
  return Blaze.Each(function() {
    return Spacebars.call(view.lookup("languages"));
  }, function() {
    return [ "\n        ", HTML.LI(HTML.A({
      href: "#",
      "class": "nanum",
      "data-action": "change-language",
      "data-language": function() {
        return Spacebars.mustache(view.lookup("code"));
      }
    }, Blaze.View("lookup:labels.name", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("labels"), "name"));
    }), "  ")), "\n      " ];
  });
}));

